.header{
    display: flex;
    width: 100%;
    padding: 15px 20px;
    justify-content: space-between;
    position: sticky;
    background-color: white;
    z-index: 100;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0,0,0,75);
}
.header__left{
    display: flex;
    justify-content: space-between;
}
.header__left > img{
    height: 40px;
}
.header__input{
    display: flex;
    align-items: center;
    background-color: #eff2f5;
    padding: 10px;
    margin-left: 10px;
    margin-left: 33px;
    border-radius: 999px;
}
.header__input > input{
    border: none;
    background-color: transparent;
    outline-width: 0px;
}
.header__center{
    display: flex;
    flex: 1;
    justify-content: center;
}
.header__option{
    color: grey;
    display: flex;
    align-items: center;
    padding: 0 30px;
    cursor: pointer;
}
.header__option:hover{
    color: blue;
    background-color: #eff2f5;
    border-radius: 10px;
    align-items: center;
    padding: 0 30px;
    border-bottom: none;
}
.header__option--active{
    border-bottom: 4px solid blue;
}
.header__info{
    display: flex;
    align-items: center;
}
.header__info> h4{
    margin-left: 10px;
}
.header__right{
    display: flex;
    color: black;
}